import React, { useState } from 'react'
import "./App.css"
import Hello from "./Components/HelloWorld.jsx"
import Tasks from "./Components/Tasks.jsx";
import AddTask from "./Components/AddTask.jsx";


const App= () => {
  const [showAddTask,setShowAddTask]=useState(false);
  const [tasks , setTasks]=useState([ //if we want to change any part of state we can use setTasks.
    {
        id:1,
        text:'Doctors Appointment',
        day:'Feb 5th at 2:30pm',
        reminder:true,
    },
    {
        id:2,
        text:'Metting at school',
        day:'Feb 6th at 1:30 pm',
        reminder:true,
    },
    {
        id:3,
        text:'Food Shopping',
        day:'Feb 5th at 2:30 pm',
        reminder:false,
    }
])
  const addTask = (task) =>{
    const id=Math.floor(Math.random()*1000)+1
    const newTask={id, ...task}
    setTasks([...tasks,newTask])
    //console.log(id);
    //console.log(task);
  }
//delete 
  const deleteTask=(id) =>{
      setTasks(tasks.filter((task)=>task.id!=id))
    }
//toogle reminder.
  const toogleReminder=(id)=>{
    setTasks(tasks.map((task)=> task.id === id
    ?{ ...task,reminder:!task.reminder}:task ///else reminder akk want to copy
    //?console.log(!task.reminder):console.log(task.reminder)
    )
    )    }
  return (
    <div className="div1"> 
      <Hello title="Assignment tracker"
      onAdd={()=>setShowAddTask(!showAddTask)} 
      showAdd={showAddTask}/>
      {showAddTask  && <AddTask onAdd={addTask} />}
      {tasks.length > 0 ? (
      <Tasks tasks={tasks} onDelete={
        deleteTask} onToggle={toogleReminder}/>)
      :('No tasks to show')}      
    </div>
    )
}
export default App



